import Vue from "vue";
import VueRouter from "vue-router";
import Login from "../views/Login.vue";
import index from "../views/index.vue";
import index_admin from "../views/Admin/index_admin";

//前台
import Home from '../views/Home'
import Contact from '../views/Contact'
import News from '../views/News'
import NewsDetail from '../views/NewsDetail'
import Product from '../views/Product'
import ProductBigBoard from '../views/ProductBigBoard'
import ProductThinBoard from '../views/ProductThinBoard'
//後台
import Admin_Index from '../views/Admin/Admin_Index'
import Admin_Add_News from '../views/Admin/Admin_Add_News'

//防止路由重複讀取錯誤
//获取原型对象上的push函数
const originalPush = VueRouter.prototype.push
//修改原型对象中的push方法
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const scrollBehavior = function (to, from, savedPosition) {
  // savedPosition 会在你使用浏览器前进或后退按钮时候生效
  // 这个跟你使用 router.go() 或 router.back() 效果一致
  // 这也是为什么我在 tab 栏结构中放入了一个 点击回退 的按钮
  if (savedPosition) {
    return savedPosition
  } else {
    // 如果不是通过上述行为切换组件，就会让页面回到顶部
    return { x: 0, y: 0 }
  }
}

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    component: index,
    children: [
      {
        path: "Login",
        name: "Login",
        component: Login,
      },
      {
        path: "",
        name: "Home",
        component: Home,
      },
      {
        path: 'Contact',
        name: 'Contact',
        component: Contact
      },
      {
        path: 'News',
        name: 'News',
        component: News
      },
      {
        path: 'NewsDetail',
        name: 'NewsDetail',
        component: NewsDetail
      },
      {
        path: 'Product',
        name: 'Product',
        component: Product
      },
      {
        path: 'ProductBigBoard',
        name: 'ProductBigBoard',
        component: ProductBigBoard
      },
      {
        path: 'ProductThinBoard',
        name: 'ProductThinBoard',
        component: ProductThinBoard
      },
    ],
  },
  {
    path: "/index_admin",
    component: index_admin,
    children: [
      {
        path: "/Admin_Add_News",
        name: "Admin_Add_News",
        component: Admin_Add_News,
      },
      {
        path: "/Admin_Index",
        name: "Admin_Index",
        component: Admin_Index,
      }
    ],
  }
];



const router = new VueRouter({
  //mode: 'history',
  //base: process.env.BASE_URL,
  scrollBehavior,
  routes,
});

export default router;
